import uniRate from '@/components/uni-rate/uni-rate.vue'
export default {
	data() {
		return {
			list: [],
			value: '',
			active: false,
			activeIndex: 0,
			text: '全部',
			textShow: false
		}
	},
	components:{
		uniRate
	},
	onLoad() {

	},
	watch: {
		activeIndex: function(val) {
			console.log(val);
			if(val === 0) {
				this.text = '全部';
			}
			if(val === 1) {
				this.text = '待办';
			}
			if(val === 2) {
				this.text = '已完成';
			}
		}
	},
	computed:{
		listData() {
			let list = JSON.parse(JSON.stringify(this.list));
			let newList = [];
			if(this.activeIndex === 0) {
				// this.text = '全部';  不能在计算属性中修改变量的值  watch监听
				return list;
			}
			if(this.activeIndex === 1) {
				// this.text = '待办';
				//checked  false
				list.forEach((item) => {
					if(!item.checked) {
						newList.push(item);
					}
				})
				return newList;
			}
			if(this.activeIndex === 2) {
				// this.text = '已完成';
				//checked true
				list.forEach((item) => {
					if(item.checked) {
						newList.push(item);
					}
				})
				return newList;
			}
		}
	},
	methods: {
		// 打开输入框
		creat() {
			if(this.active) {
				this.close();
			}else {
				this.open();
			}
			
		},
		open() {
			this.active = true;
			this.$nextTick(() => {
				setTimeout(() => {
					this.textShow = true;
				}, 50)
			})
		},
		close() {
			this.textShow = false;
			this.$nextTick(() => {
				setTimeout(() => {
					this.active = false;
				}, 350)
			})
		},
		add() {
			if(!this.value) {
				uni.showToast({
					title: '请输入内容',
					icon: 'none'
				})
				return;
			}
			this.list.unshift({
				content: this.value,
				id: 'id' + new Date().getTime(),
				checked: false
			})
			this.value = '';
			// this.active = false;
			this.close();
		},
		finsh(id) {
			console.log(id);
			let index = this.list.findIndex((item) => {
				return item.id === id;
			})
			this.list[index].checked = !this.list[index].checked;
			
		},
		tab(index) {
			this.activeIndex = index;
		}
	}
}
